@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap);
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

/* loader */
.spinner{
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 9999;
    background-color: rgba(0, 0, 0, 0.385);
}

.form-btn{
    width: 150px !important;
    margin-top: 30px !important;
}
.main {
    height: 100vh;
    padding: 10px;
    width: 100vw;
}

.layout {
    display: flex;
    background: linear-gradient(to bottom right, #ff966d, #fa538d, #89379c);
}

.sidebar {
    position: -webkit-sticky;
    position: sticky;
    top: 0px;
    left: -10px;
    margin-left: 10px;
    height: 96vh;
    width: 300px;
    border-radius: 5px;
    background-color: #5ae1ff;
    box-shadow: 0px 0px 2px gray;
    margin-right: 20px;
    color: white;
}

.ham-menu {
    display: none;
    z-index: 3;
    position: fixed;
    top: 10px;
    left: 20px;
    scale: 1.6;
}


.content {
    width: 100%;
    height: 100%;
}

.header {
    height: 10vh;
    margin-bottom: 20px;
    box-shadow: 0px 0px 2px gray;
    background-color: #5ae1ff;
}

.body {
    padding: 40px;
    margin-bottom: 20px;
    box-shadow: 0px 0px 2px gray;
    border: 2px solid black;
    background: #f3aeef;
}

.logo {
    font-size: 1rem;
    margin: 10px 0px;
}

.logo h6 {
    font-size: 1.5rem;
    margin: 10px 0px;
    text-align: center;
}

.menu {
    margin-top: 50px;
}

.menu-item {
    margin-top: 30px;
}

.menu-item a {
    color: white;
    text-decoration: none;
    font-size: 1.2rem;
}

.menu-item i {
    font-size: 1.2rem;
    margin: 0px 15px;
}

.active {
    background-color: white;
    padding: 10px;
}

.active a,
.active i {
    color: rgb(121, 24, 24);
}

.header-content {
    display: flex;
    align-items: center;
    height: 50px;
    justify-content: center;
    margin: 0px 150px;
}

.header-content i {
    margin-right: 20px;
    font-size: 1.2rem;
}

.header-content a {
    text-decoration: none;
    color: blue;
    font-size: 1.2rem;
    margin: 0 10px;
    text-transform: uppercase;
}

.bell{
    grid-gap: 0;
    gap: 0;
}

@media screen and (max-width:800px) {

    .hidebar {
        display: none;
    }

    .ham-menu {
        display: block;
    }

    .hidebar .ham-menu {
        color: white;
    }

    .sidebar {
        position: fixed;
        z-index: 2;
        top: 0px;
        left: -10px;
        border-radius: 0px;
        height: 100vh;
    }
}

*,
html,
body {
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Poppins', sans-serif;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(to bottom right, #ff966d, #fa538d, #89379c);
}

.container {
    width: 50vw;
    height: 60vh;
    display: grid;
    grid-template-columns: 100%;
    grid-template-areas: "login";
    box-shadow: 0 0 17px 10px rgb(0 0 0 / 30%);
    border-radius: 20px;
    background: white;
    overflow: hidden;
}

.design img {
    height: 220px;
    width: 250px;
}

.design i {
    margin-left: 20px;
}

.login {
    grid-area: login;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    background: white;
}

.login h3.title {
    margin: 15px 0;
    color:#a414f7;
}

.text-input {
    background: #e6e6e6;
    height: 40px;
    display: flex;
    width: 60%;
    align-items: center;
    border-radius: 10px;
    padding: 0 15px;
    margin: 5px 0;
}

.text-input input {
    background: none;
    border: none;
    outline: none;
    width: 100%;
    height: 100%;
    margin-left: 10px;
}

.text-input i {
    color: #686868;
}

::-webkit-input-placeholder {
    color: #9a9a9a;
}

::placeholder {
    color: #9a9a9a;
}

.login-btn {
    width: 68%;
    padding: 10px;
    color: white;
    background: linear-gradient(to right, #ff966d, #fa538d, #89379c);
    border: none;
    border-radius: 20px;
    cursor: pointer;
    margin-top: 10px;
}

a {
    font-size: 12px;
    color: #9a9a9a;
    cursor: pointer;
    -webkit-user-select: none;
            user-select: none;
    text-decoration: none;
}

a.forgot {
    margin-top: 15px;
}

.create i {
    color: #9a9a9a;
    margin-left: 10px;
}


@media screen and (max-width:768px) {
    .login h3 {
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        margin: 20px auto !important;
    }

    .login-btn{
        margin-left: 10%;
    }

    .login .design{
        display: none;
    }

   
}


* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Poppins", sans-serif;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(to bottom right, #ff966d, #fa538d, #89379c);
}

.container {
  padding: 20px;
  width: 70vw;
  max-width: 700px;
  height: 70vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 30px;
  gap: 30px;
  justify-content: center;
  box-shadow: 0 0 17px 10px rgb(0 0 0 / 30%);
  border-radius: 20px;
  background: white;
  overflow: hidden;
}

.some h2{
  color: #f94081;
}

.form-content {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  width: 500px;
}

.design {
  grid-area: design;
  /* display: none; */
  position: relative;
}

.some {
  display: flex;
  align-items: center;
  grid-gap: 20px;
  gap: 20px;
}

.some img {
  width: 60px;
  height: 60px;
  padding: 0px;
  margin: 0px;
}


.design h6 {
  display: flex;
  font-family: "Barlow", sans-serif;
  font-family: "Geologica", sans-serif;
  font-family: "Pacifico", cursive;
  font-family: "PT Sans", sans-serif;
  font-family: "Satisfy", cursive;
  justify-content: center;
  align-items: center;
  display: none;
}

.frm-itm {
  margin: 0px;
}

.register {
  /* grid-area: login; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: auto;
  width: 300px;
  position: relative;
  background: white;
}

.register h3.title {
  margin: 15px 0;
}

.text-input {
  background: #e6e6e6;
  height: 30px;
  display: flex;
  width: 80%;
