  bottom: 20px;
}

a {
  font-size: 12px;
  color: #9a9a9a;
  cursor: pointer;
  -webkit-user-select: none;
          user-select: none;
  text-decoration: none;
}

a.forgot {
  margin-top: 15px;
}

.login form {
  padding-left: 20px;
}

h3.text-center {
  text-align: left !important;
  color: #a414f7;
}

@media (min-width: 768px) {
  .container {
    flex-direction: row;
  }

  .design h6 {
    display: flex;
  }
}

@media screen and (max-width:768px) {
  .register{
    width: 250px;
    margin-left: 10%;
  }
}
